html {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  height: 100%;
  width: 100%;
  opacity: 1;
  background: radial-gradient(circle, #0000 20%, #000 20% 80%, #0000 80%, #0000) 0 0 / 40px 40px, radial-gradient(circle, #0000 20%, #000 20% 80%, #0000 80%, #0000) 20px 20px / 40px 40px, linear-gradient(#525252 1.6px, #0000 1.6px) 0 -.8px / 20px 20px, linear-gradient(90deg, #525252 1.6px, #000 1.6px) -.8px 0 / 20px 20px;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body * {
  user-select: none;
}

body > #entry {
  cursor: grab;
  position: absolute;
  inset: 0;
}

body > .open-settings {
  position: absolute;
  top: 40px;
  right: 40px;
}

body > #settings {
  pointer-events: none;
  opacity: 0;
  backdrop-filter: blur(10px) brightness(20%);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  transition: opacity .5s;
  display: flex;
  position: absolute;
  inset: 0;
}

body > #settings.open {
  pointer-events: all;
  opacity: 1;
}

body > #settings > h2 {
  color: #fff;
  font-family: sans-serif;
}

body > #settings > .currency-toggle-options {
  color: #fff;
  min-width: 600px;
  max-height: 400px;
  flex-direction: column;
  align-items: stretch;
  gap: 10px;
  display: flex;
  overflow: scroll;
}

body > #settings > .currency-toggle-options > .currency-toggle-wrap {
  align-items: center;
  display: flex;
}

body > #settings > .currency-toggle-options > .currency-toggle-wrap > label {
  flex-grow: 1;
  font-family: sans-serif;
  font-size: 24px;
}

body > #settings > .currency-toggle-options > .currency-toggle-wrap > input {
  height: 40px;
  width: 40px;
  flex-grow: 0;
}

body > #sidebar {
  width: 40vw;
  height: 100vh;
  backdrop-filter: brightness(20%) blur(10px);
  color: #fff;
  box-sizing: border-box;
  grid-template: "content close" 1fr
                 "options close"
                 / 1fr auto;
  gap: 10px;
  padding: 20px;
  font-family: sans-serif;
  transition: transform .2s;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

body > #sidebar > .content {
  grid-area: content;
  overflow-y: auto;
}

body > #sidebar > .close {
  cursor: pointer;
  grid-area: close;
  align-items: center;
  display: flex;
}

body > #sidebar.open {
  transform: translateX(0%);
}

body > #loading {
  backdrop-filter: blur(10px) brightness(20%);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

body > #loading > .loading-bar {
  height: 50px;
  width: 400px;
  background-color: #a9a9a9;
  border-radius: 25px;
  overflow: hidden;
}

body > #loading > .loading-bar > .loaded {
  height: 100%;
  width: 0%;
  box-shadow: 0px 0px 10px 5px lighblue;
  background-color: #add8e6;
  transition: width .1s;
}

body > #loading > #loading-text {
  color: #fff;
  font-family: sans-serif;
}

